//心率柱状图
function heartBarChart(obj) {
    var num = changeTime(window.RUNNINGTIME);
    var arr = [];
    for (var i = 0; i < obj.length; i++) {
        console.log('=================================', Math.floor(num * obj[i] / 100));
        var time = Math.floor(num * obj[i] / 100);
        time = getTimes(time);
        console.log('===================', time);
        //转时间：
        arr.push(time);
    }

    //中英文切换
    var categories = '';
    var language = $.getParam('language');

    if (language === 'zh_cn') {
        categories = ["热身<br><span style='color: rgba(0,0,0,0.2)'>" + arr[0] + "</span>", "减脂<br><span style='color: rgba(0,0,0,0.2)'>" + arr[1] + "</span>", "心肺<br><span style='color: rgba(0,0,0,0.2)'>" + arr[2] + "</span>", "肌力<br><span style='color: rgba(0,0,0,0.2)'>" + arr[3] + "</span>", "最大<br><span style='color: rgba(0,0,0,0.2)'>" + arr[4] + "</span>"];
    } else if (language === 'en') {
        categories = ["1<br><span style='color: rgba(0,0,0,0.2)'>" + arr[0] + "</span>", "2<br><span style='color: rgba(0,0,0,0.2)'>" + arr[1] + "</span>", "3<br><span style='color: rgba(0,0,0,0.2)'>" + arr[2] + "</span>", "4<br><span style='color: rgba(0,0,0,0.2)'>" + arr[3] + "</span>", "5<br><span style='color: rgba(0,0,0,0.2)'>" + arr[4] + "</span>"];
    } else if (language === 'zh_tw' || language === 'zh_hk') {
        categories = ["熱身<br><span style='color: rgba(0,0,0,0.2)'>" + arr[0] + "</span>", "減脂<br><span style='color: rgba(0,0,0,0.2)'>" + arr[1] + "</span>", "心肺<br><span style='color: rgba(0,0,0,0.2)'>" + arr[2] + "</span>", "肌力<br><span style='color: rgba(0,0,0,0.2)'>" + arr[3] + "</span>", "最大<br><span style='color: rgba(0,0,0,0.2)'>" + arr[4] + "</span>"];
    }

    //条状图数据json
    var json = {
        chart: {
            type: 'column',
        },
        title: {
            text: null,
        },
        credits: {
            enabled: false,
        },
        legend: {
            enabled: false,
        },
        plotOptions: {
            series: {
                pointWidth: 20
            },
            column: {
                enableMouseTracking: false
            }
        },
        xAxis: {
            title: {
                text: null,
            },
            lineColor: 'rgba(0,0,0,0.1)',
            lineWidth: 1,
            categories: categories,
            tickLength: 0,
        },
        yAxis: {
            title: {
                text: null,
            },
            softMax: 101,
            labels: {
                enabled: null
            },
            gridLineWidth: 0,
        },
        series: [{
            color: '#F3222E',
            data: obj,
            dataLabels: {
                enabled: true,
                format: '{point.y:1f}%',
                color: 'rgba(0,0,0,0.8)',
                style: {
                    fontFamily: 'Akrobat-SemiBold',
                    fontSize: '12px',
                    color: 'rgba(0,0,0,0.8)',
                },
                x: 0,
            }
        }]
    };
    var chart = Highcharts.chart('heart-chart', json);
}

//心率折线图(改)
function heartLineChart(obj) {

    var control_num = 30;

    var length = obj.length >= control_num ? control_num : obj.length;
    var num_padding = obj.length / control_num > 1 ? Math.floor(obj.length / control_num) : 1;
    var arr = [];
    var index_num = window.RUNNINGTIME.split(':');
    //跑步分钟数
    index_num = index_num[0];

    var test_num = +index_num;
    var my_step = 1;

    if (test_num <= 14) {
        my_step = 1;
    } else if (14 < test_num <= 28) {
        my_step = 2;
    } else if (28 < test_num <= 42) {
        my_step = 3;
    } else if (42 < test_num <= 54) {
        my_step = 4;
    } else if (54 < test_num) {
        my_step = 6;
    }


    if (obj.length <= control_num) {
        for (var i = 0; i < length; i++) {
            arr.push(obj[i]);
        }
    } else {
        for (var i = 0; i < length; i++) {
            if (obj[i + i * num_padding]) {
                console.log(i + i * num_padding);
                arr.push(obj[i + i * num_padding])
            }
        }
    }

    index_num = Number(index_num) / arr.length;

    for (var i = 0; i < arr.length; i++) {
        if (i == 0) {
            arr[i][0] = i * index_num
        } else {
            arr[i][0] = i * index_num + index_num
        }
        //取整数位
        obj[i][1] = +obj[i][1].toFixed(0);
    }

    //面积图json
    var areaJson = {
        chart: {
            type: 'area',
        },
        title: {
            text: null,
        },
        credits: {
            enabled: false,
        },
        legend: {
            enabled: false,
        },
        xAxis: {
            title: {
                text: null,
            },
            categories: [],
            lineColor: 'rgba(0,0,0,0.1)',
            lineWidth: 1,
            labels: {
                enabled: true,
                style: {
                    color: 'rgba(0,0,0,0.2)',
                    fontSize: '10px'
                },
                step: my_step,
            },
            tickLength: 0,
        },
        yAxis: {
            title: {
                text: null,
            },
            labels: {
                enabled: true,
                style: {
                    color: 'rgba(0,0,0,0.2)',
                    fontSize: '10px'
                }
            },
            gridLineWidth: 1,
        },
        plotOptions: {
            area: {
                fillColor: {
                    linearGradient: {
                        x1: 0,
                        y1: 0,
                        x2: 0,
                        y2: 1
                    },
                    stops: [
                        [0, 'rgba(243,34,46,.5)'],
                        [1, 'rgba(243,34,46,0)']
                    ]
                },
                enableMouseTracking: false
            }
        },
        series: [{
            data: arr,
            zones: [{
                value: 3, //小于
                color: 'rgba(255,255,255,0)',
            }, {
                color: '#F3222E'
            }
            ],
            marker: {
                enabled: false,
            },
            dataLabels: {
                enabled: false,
                format: '{point.y:.1f}%',
                color: 'rgba(0,0,0,0.8)',
            }
        }]
    };
    var lineCharts = Highcharts.chart('heart-real-chart', areaJson);

    my_getChartMin.getChartMax(lineCharts, my_getChartMin.getMaxAndMin(areaJson), 'normal');
}

//每公里配速柱状图
function kilometreBarChart(obj) {

    var data = [];
    var categories = [];

    var pointWidth = 10;

    if(obj.length <=5 ){
        pointWidth = 20
    }else if(obj.length <= 10 && obj.length > 5){
        pointWidth = 18;
    }else if(obj.length <= 15 && obj.length > 10){
        pointWidth = 12;
    }else if(obj.length <= 20 && obj.length > 15){
        pointWidth = 10;
    }else if(obj.length >20){
        pointWidth = 8
    }

    console.log(obj.length);
    console.log('当前的柱子宽度为：：：：',pointWidth);

    data = obj;
    for (var i = 0; i < obj.length; i++) {
        categories.push(i + 1)
    }

    //柱状图数据json
    var json = {
        chart: {
            type: 'column',
        },
        title: {
            text: null,
        },
        credits: {
            enabled: false,
        },
        legend: {
            enabled: false,
        },
        plotOptions: {
            series: {
                pointWidth: pointWidth
            },
            column: {
                enableMouseTracking: false,
                dataLabels: {
                    enabled: true,
                    formatter: function () {
                        return Highcharts.numberFormat(this.point.y, 2, "'") + "''"
                    },
                    color: 'rgba(0,0,0,0.8)',
                    style: {
                        fontFamily: 'Akrobat-SemiBold',
                        fontSize: '12px',
                        color: 'rgba(0,0,0,0.8)',
                    },
                    x: 3,
                }
            }
        },
        xAxis: {
            title: {
                text: null,
            },
            lineColor: 'rgba(0,0,0,0.1)',
            lineWidth: 1,
            tickLength: 0,
            categories: categories
        },
        yAxis: {
            title: {
                text: null,
            },
            labels: {
                enabled: null
            },
            softMax: 11,
            gridLineWidth: 0,
        },
        series: [{
            color: '#00BB00',
            data: data,
        }]
    };
    var chart = Highcharts.chart('pace-chart', json);
}

//实时配速折线图
function kilometreLineChart(obj) {

    var new_arr = [];
    var length = obj.length;
    var index_num = window.RUNNINGTIME.split(':');
    index_num = index_num[0];

    var test_num = +index_num;
    var my_step = 1;

    if (test_num <= 14) {
        my_step = 1;
    } else if (14 < test_num <= 28) {
        my_step = 2;
    } else if (28 < test_num <= 42) {
        my_step = 3;
    } else if (42 < test_num <= 54) {
        my_step = 4;
    } else if (54 < test_num) {
        my_step = 6;
    }

    index_num = Number(index_num) / length;

    for (var i = 0; i < obj.length; i++) {
        obj[i][0] = i;
        if (obj[i][1] > 30) {
            obj[i][1] = 30.00;
            obj[i][1] = +obj[i][1].toFixed(2);
        } else if (obj[i][1] < 3) {
            obj[i][1] = 3.00;
            obj[i][1] = +obj[i][1].toFixed(2);
        }
        new_arr.push([i, obj[i][1]])
    }

    for (var i = 0; i < new_arr.length; i++) {
        if (i == 0) {
            new_arr[i][0] = new_arr[i][0] * index_num
        } else {
            new_arr[i][0] = new_arr[i][0] * index_num + index_num;
        }
    }

    console.log('新生产的数据', new_arr);

    //面积图json（实时配速）
    var areaJson = {
        chart: {
            type: 'area',
        },
        title: {
            text: null,
        },
        credits: {
            enabled: false,
        },
        legend: {
            enabled: false,
        },
        xAxis: {
            title: {
                text: null,
            },
            categories: [],
            lineColor: 'rgba(0,0,0,0.1)',
            lineWidth: 1,
            allowDecimals: false,
            labels: {
                enabled: true,
                style: {
                    color: 'rgba(0,0,0,0.2)',
                    fontSize: '10px'
                },
                step: my_step,
            },
            tickLength: 0,
        },
        yAxis: {
            title: {
                text: null,
            },
            labels: {
                enabled: false,
                style: {
                    color: 'rgba(0,0,0,0.2)',
                    fontSize: '10px'
                }
            },
            gridLineWidth: 0,
        },
        plotOptions: {
            area: {
                fillColor: {
                    linearGradient: {
                        x1: 0,
                        y1: 0,
                        x2: 0,
                        y2: 1
                    },
                    stops: [
                        [0, 'rgba(0,187,0,0.5)'],
                        [1, 'rgba(0,187,0,0)']
                    ]
                },
                enableMouseTracking: false,
            }
        },
        series: [{
            color: '#00BB00',
            data: new_arr,
            marker: {
                enabled: false,
            },
            dataLabels: {
                enabled: false,
                format: '{point.y:.1f}%',
                color: 'rgba(0,0,0,0.8)',
            },
            type: 'area',
        }]
    };
    var lineCharts = Highcharts.chart('pace-real-chart', areaJson);
    my_getChartMin.getChartMax(lineCharts, my_getChartMin.getMaxAndMin(areaJson), 'real-pace');
}

