//时间转成秒
function changeTime(time) {
    var string;
    var arr = time.split(':');
    string = Number(arr[0]) * 60 + Number(arr[1]);
    console.log(string);
    return string;
}

function getTimes(time) {
    var string = '';
    var time1 = Math.floor(time / 60);
    var time2 = time % 60;
    if (time1 < 10) {
        time1 = '0' + time1;
    }
    if (time2 < 10) {
        time2 = '0' + time2;
    }
    string = time1 + ":" + time2;
    return string;
}

