/**
 * 在曲线中标记出最大及最小值。
 * @param charts
 */
function getChartMax(charts, arr, type) {

    console.log(arr);

    var point = charts.series[0].data[arr[0].index];
    var pointMax = charts.series[0].data[arr[1].index];
    var MinNum, MaxNum = '';
    var option = {
        minGeneral3: 0,
        minGeneral2: 1.3, //ok
        minGeneral1: 4,

        maxGeneral3: -3,
        maxGeneral2: -1.2, //ok
        maxGeneral1: 1,

        minPace2: -5,
        minPace3: -4.5,
        maxPace2: -6,
        maxPace3: -8,
    };

    arr[0].value += 0;
    arr[1].value += 0;

    if (isInteger(arr[0].value) === false) {
        arr[0].value = arr[0].value.toFixed(2);
    }

    if (isInteger(arr[1].value) === false) {
        arr[1].value = arr[1].value.toFixed(2);
    }

    console.log(arr[0].value, arr[1].value);

    if (type !== 'real-pace') {
        console.log('real-pace');
        //Min
        if (arr[0].value > 99) {
            //三位数
            MinNum = option.minGeneral3
        } else if (arr[0].value > 9 && arr[0].value <= 99) {
            //两位数
            MinNum = option.minGeneral2;
        } else {
            //个位
            MinNum = option.minGeneral1
        }
        //Max
        if (arr[1].value > 99) {
            MaxNum = option.maxGeneral3
        } else if (arr[1].value > 9 && arr[1].value <= 99) {
            MaxNum = option.maxGeneral2;
        } else {
            MaxNum = option.maxGeneral1;
        }
    } else {

        //添加补充量
        if (arr[0].value > 9) {
            //10+
            MinNum = option.minPace3;
        } else {
            MinNum = option.minPace2;
        }

        if (arr[1].value > 9) {
            //10+
            MaxNum = option.maxPace3;
        } else {
            MaxNum = option.maxPace2;
        }

        if (arr[0].value === 3) {
            arr[0].value = "3'00''";
        } else {
            arr[0].value = arr[0].value.toString().replace(".", "'") + "''";
        }

        if (arr[1].value === 30) {
            arr[1].value = "30'00''";
        } else {
            arr[1].value = arr[1].value.toString().replace(".", "'") + "''";
        }
    }

    console.log(MinNum + '-----------' + MaxNum);

    var text = charts.renderer.text(
        arr[0].value,
        point.plotX + charts.plotLeft + MinNum - 3,
        point.plotY + charts.plotTop + 15
    ).attr({
        zIndex: 5,
    }).css({color: '#fff', fontSize: '12px', fontFamily: 'Akrobat-SemiBold'}).add();

    var images_min = charts.renderer.image(
        '/images/Cursor_bottom.png',
        point.plotX + charts.plotLeft - 13,
        point.plotY + charts.plotTop + 0,
        32,
        18
    ).attr({zIndex: 1}).add();

    //Max
    var images_max = charts.renderer.image(
        '/images/Cursor_top.png',
        pointMax.plotX + charts.plotLeft - 12,
        pointMax.plotY + charts.plotTop - 18,
        32,
        18
    ).attr({zIndex: 1}).add();

    //最大值标记
    var textMax = charts.renderer.text(
        arr[1].value,
        pointMax.plotX + charts.plotLeft + MaxNum,
        pointMax.plotY + charts.plotTop - 7
    ).attr({
        zIndex: 6,
    }).css({color: '#fff', fontSize: '12px', fontFamily: 'Akrobat-SemiBold'}).add();
}

/**
 * 获取到表格所用数据的最大最小值所在的位置
 * @param json
 * @returns {*[]}
 */
function getMaxAndMin(json) {

    var status = false;

    var data = json.series[0].data;
    var control_num = 0;

    console.log("DATA======", data);
    console.log(data[0][1]);
    console.log(data[data.length - 1][1]);

    //第一个不为0的数据

    for (var i = 0; i < data.length; i++) {
        if (data[i][1] === 0) {
            data.shift();
            status = true;
            control_num++
        }
        ;
    }

    // if (data[0][1] == 0) {
    //     console.log('第一位为 0');
    //     data.shift();
    //     status = true;
    // }

    if (data[data.length - 1][1] == 0) {
        console.log('最后一位为 0');
        data.pop();
        status = true;
    }

    for (var i = 0; i < data.length; i++) {
        data[i] = data[i][1];
    }
    var min = data.indexOf(Math.min.apply(Math, data));
    var max = data.indexOf(Math.max.apply(Math, data));
    var arr;

    if (status == true) {
        arr = [{index: min + control_num, value: json.series[0].data[min]}, {
            index: max + control_num,
            value: json.series[0].data[max]
        }];
    } else {
        arr = [{index: min, value: json.series[0].data[min]}, {index: max, value: json.series[0].data[max]}];
    }

    return arr;
}

function getMaxAndMinForHeart(json) {

    var data = json.series[0].data;
    var newData = [];
    for (var i = 0; i < data.length; i++) {
        newData.push(data[i][1]);
    }

    var minIndex = getMinNum(newData);
    var maxIndex = getMaxNum(newData);

    var arr;
    arr = [{index: minIndex, value: newData[minIndex]}, {index: maxIndex, value: newData[maxIndex]}];

    return arr;


    function getMinNum(data) {
        //需要值和位置
        var minIndex = data[0] == 0 ? getNumberNotZero(data) : 0;
        var minNum = data[minIndex];

        var length = data.length;
        for (var i = 0; i < length; i++) {
            if (data[i] < minNum && data[i] !== 0) {
                minNum = data[i];
                minIndex = i;
            }
        }

        return minIndex;
    }

    function getMaxNum(data) {

        var max = data[0];
        var maxIndex = 0;
        var length = data.length;
        for (var i = 0; i < length; i++) {
            if (data[i] > max) {
                max = data[i];
                maxIndex = i
            }
        }

        return maxIndex;

    }

    function getNumberNotZero(data) {
        var num = 0;
        for (var i = 0; i < length; i++) {
            if (data[i] !== 0) {
                num = i;
                return num;
            }
        }
    }


    Array.prototype.min = function () {
        var min = this[0];
        var len = this.length;
        for (var i = 1; i < len; i++) {
            if (this[i] < min) {
                min = this[i];
            }
        }
        return min;
    }

}

function isInteger(obj) {
    return obj % 1 === 0
}

exports.getChartMax = getChartMax;
exports.getMaxAndMin = getMaxAndMin;
exports.getMaxAndMinForHeart = getMaxAndMinForHeart;



