    var chartFrom = require('../running/charts');

    //加载数据生成页面：
    window.onload = function () {

        var userAgent = MygetUserAgentFun();

        try {
            if (userAgent == 'ios') {
                window.webkit.messageHandlers.PaceHistoryForDm.postMessage({method: 'WebIsReady'});
            } else {
                PaceHistoryForDm.WebIsReady(2);
            }
        } catch (e) {
            console.log('连接客户端失败');
            var testData = {
                type: 19,
                time: 19,                               //运动时长
                consume: 281,                           //消耗
                num: 10
            };

            var string = JSON.stringify(testData);
            CallBackBodyBuildingData(string);
        }

    };

    function CallBackBodyBuildingData(string) {

        var testData = JSON.parse(string);

        console.log(testData);

        setBodyData(testData);

    }

    //设置基本信息
    function setBodyData(obj) {

        var bannerData = selectBodyBodying(obj.type);

        //封面信息
        $('.building-box-title .building-title').text(bannerData.title);

        if (obj.type >= 15) {
            $('.building-box-title a').attr('href', 'train_bodybuilding.html?list=' + obj.type);
            $('.building-box-title .building-num').html("训练动作：" + bannerData.num + "个" + "<img class='building-arrow' src='/images/arrow_banner@2x.png' alt='arrow'>");
        } else {
            $('.building-box-title .building-num').html("训练动作：" + obj.num + "个" + "<img class='building-arrow' src='/images/arrow_banner@2x.png' alt='arrow'>");
            $('.building-box-title a').attr('href', 'javascript:void(0)');
        }

        $('#building-banner').css({
            background: "url(" + bannerData.background + ") no-repeat center",
            backgroundSize: 'cover',
        });

        //基本数据
        console.log(obj);
        var box = $('.pace-data-box .pace-data-normal .pace-normal-content span');

        if(obj.time.toString().indexOf(':') === -1){
            window.RUNNINGTIME = obj.time + ":00";
        }else {
            window.RUNNINGTIME = obj.time
        }

        box.eq(0).text(obj.time + ' 分钟');
        box.eq(1).text(obj.consume + ' 千卡');
        box.eq(2).text(obj.recovery);
    }


